import * as React from "react"
import {
  ChakraProvider,
  Box,
  Button,
  Text,
  Link,
  VStack,
  Grid,
  Input,
  theme,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  TableContainer,
  Divider,
} from "@chakra-ui/react"
import { ColorModeSwitcher } from "./ColorModeSwitcher"
import { Logo } from "./Logo"
import { fromBech32, toBech32 } from "@cosmjs/encoding";

export const App = () => {
  const [value, setValue] = React.useState('')
  const [elegible, setElegible] = React.useState(false)
  const [showRes, setShowRes] = React.useState(false)
  const [address, setAddress] = React.useState('')
  const [total, setTotal] = React.useState(0)

  const checkAddress = () => {
    if (!value) return
    console.log("Address", value)
    const {prefix, data} = fromBech32(value)
    if (prefix !== "cosmos") {
      alert('invalid address')
      return
    }

    setShowRes(false)
    const addr = toBech32("govgen", data)

    fetch(`https://api.govgen.giansalex.dev/cosmos/bank/v1beta1/balances/${addr}`)
    .then((response) => response.json())
    .then((data) => {
      const amount = Number(data.balances[0].amount) / 1000000
      setAddress(addr)
      setTotal(amount)
      setElegible(true)
    }).catch((err) => {
      setElegible(false)
    }).finally(() => setShowRes(true));
  }

  return (
    <ChakraProvider theme={theme}>
      <Box textAlign="center" fontSize="xl">
        <Grid minH="100vh" p={3}>
          {/* <ColorModeSwitcher justifySelf="flex-end" /> */}
          <Grid marginTop={8} >
            <VStack spacing={6}>
              <Logo h="40vmin" pointerEvents="none" />
              <Text>
                $GovGen Airdrop
              </Text>
              <Input 
                placeholder='cosmos address' onChange={(event) => setValue(event.target.value)} 
                width={"xl"}  
              />
              <Button colorScheme='blue' onClick={checkAddress}>Check</Button>
              {showRes && <Divider />}
              {showRes && <Box width={"xl"}>
                <TableContainer>
                  <Table variant='simple' fontSize={"16px"}>
                    {elegible ? (
                      <>
                      <Thead>
                      <Tr>
                        <Th>Address</Th>
                        <Th isNumeric>Amount</Th>
                      </Tr>
                    </Thead>
                    <Tbody>
                      <Tr>
                        <Td>{address}</Td>
                        <Td isNumeric>{total}</Td>
                      </Tr>
                    </Tbody>
                      </>
                    ): <TableCaption>Not address found</TableCaption>}
                  </Table>
                </TableContainer>
              </Box>}
            </VStack>
          </Grid>
        </Grid>
      </Box>
    </ChakraProvider>
  )  
}